import axios from 'axios';
import qs from 'qs';
// axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? 'http://39.98.74.46:8081' : "";
// axios.defaults.baseURL = 'http://192.168.0.126:8081'
//http://localhost:9001
// axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? 'http://192.168.43.67:8090' : "";
axios.defaults.timeout = 10000;
const service = axios.create({
    retry: 2 ,// ,
    retryInterval: 1000 // 1s
});

service.interceptors.request.use(function (config) {
    // ,token,(js-cookie),store
    config.method === 'POST'
        ? config.data = qs.stringify({...config.data})
        : config.params = {...config.params};
    // config.headers['Content-type'] = 'application/x-www-form-urlencoded;chartset=UTF-8';
    return config
}, function (error) {
    return Promise.reject(error)
});

service.interceptors.response.use(
    response => {
        if (response.status === 200) {
            return response.data
        } else if (response.status === 500) {
            //
            //console.log('0000')
        }
    } ,
    error => {
        //console.log(error)
        //  ,
        const config = error.config;
        if(!config || !config.retry) return Promise.reject(error);
        // 
        config.retryCount = config.retryCount || 0;
        // 
        if(config.retryCount >= config.retry){
            return Promise.reject(error)
        }
        config.retryCount += 1;
        // 
        const back = new Promise(function(resolve){
            setTimeout(() => {
                resolve()
            },config.retryInterval || 1)
        });
        // axios
        return back.then(() => {
            return service(config)
        })
    }
);

export default service
