  position: absolute;
  left: 0;
  right: 0;
  height: 42px;
  max-height: 42px;
  line-height: 42px;
  margin-bottom: 10px;
  margin-left: 20px;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  background-color: #ffffff;
  transition: all 3s ease-in-out;
}

.titleNav::-webkit-scrollbar {
  display: none;
}

.title {
  position: relative;
  cursor: pointer;
  /* background-color: #d9d9d9; */
  /* color: #4d4d4d; */
  border: 0px;
  padding-left: 15px;
  font-size: 14px;
  /* box-shadow: 1px 1px 1px 1px #999999; */
}

.active:before {
  content: " ";
  width: 8px;
  height: 8px;
  background-color: #ffffff;
  /*opacity: 0.5;*/
  border-radius: 5px;
  z-index: 1000;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.active {
  background-color: #03A9F4;
  color: #ffffff;
}

.marginR {
  margin-right: 6px;
}

[leftIcon],
[rightIcon] {
  position: absolute;
  cursor: pointer;
  height: 42px;
  line-height: 42px;
  margin-top: -5px;
  z-index: 500;
}

[leftIcon] {
  left: 0;
  top: 6px;
  box-shadow: 2px 0 5px 0 #bfbfbf;
}

[rightIcon] {
  right: 0;
  top: 6px;
  box-shadow: 0 2px 5px 0 #bfbfbf;
}

.titleNavBox {
  position: fixed;
  height: 42px;
  z-index: 200;
  box-shadow: 0 1px 3px 0 #595959;
}
</style>